<template>
  <div class="">
    <rightStickIcon />
    <v-card class="mx-auto mt-n3">
      <v-img
        :src="require('../assets/images/privacyPolicy.jpg')"
        class="my-3"
        contain
      />
      <v-card-title
        class="justify-center text-uppercase page-title"
        :class="{ 'page-titleSm': $vuetify.breakpoint.smAndDown }"
      >
        Chennai Airport
      </v-card-title>
      <v-card-subtitle
        class="text-center"
        :class="{ 'page-subtitleSm': $vuetify.breakpoint.smAndDown }"
      >
        <h1 :class="$vuetify.breakpoint.smAndDown ? 'mt-5' : 'mt-0'">
          Feedback
        </h1>
      </v-card-subtitle>
      <v-container>
        <v-row
          class="mt-0 mb-5"
          v-scrollanimation
          align="center"
          :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
        >
          <v-col cols="12" md="12">
            <v-card flat>
              <v-card-text>
                <div
                  class="text--primary text-center"
                  :class="$vuetify.breakpoint.smAndDown ? 'mx-4' : 'mx-10'"
                >
                  <p :class="$vuetify.breakpoint.smAndDown ? 'mx-0' : 'mx-10'">
                    We always strive to offer you a memorable experience at our
                    airport. If for any reason you are unsatisfied with our
                    services, or have any suggestions for improvement, we
                    welcome any of your feedback. We work tirelessly to enhance
                    our services and to be of better assistance to you and your
                    fellow travellers.
                  </p>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <div class="feedbackFormBlock">
        <v-card
          v-scrollanimation
          class="mb-10"
          :class="{ ChennaiAirportSm: $vuetify.breakpoint.smAndDown }"
        >
          <v-img src="../assets/images/feedback-img.jpg"> </v-img>
        </v-card>

        <v-card
          flat
          class="rounded-xl feedbackForm"
          :class="{ feedbackFormSm: $vuetify.breakpoint.smAndDown }"
        >
          <v-card flat>
            <v-card-subtitle
              class="text-center formTitle"
              :class="{ 'page-subtitleSm': $vuetify.breakpoint.smAndDown }"
            >
              <h1 :class="$vuetify.breakpoint.smAndDown ? 'mt-5' : 'mt-0'">
                Feedback
              </h1>
            </v-card-subtitle>
          </v-card>
          <div
            class="formSection"
            :class="{ formSectionSm: $vuetify.breakpoint.smAndDown }"
          >
            <v-form v-model="valid" @submit="handleSubmit">
              <v-container>
                <v-row :class="$vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-2'">
                  <v-col
                    cols="12"
                    :class="{ modalText2: $vuetify.breakpoint.smAndDown }"
                  >
                    <div class="justify-space-between">
                      <div class="text-h5 text--primary my-5">
                        Contact Information
                      </div>
                      <div class="text--primary my-5">
                        Please Provide your contact information to reach you
                      </div>
                    </div>
                  </v-col></v-row
                >

                <v-row>
                  <v-col cols="12" md="6">
                    <v-text-field
                      v-model="formData.firstname"
                      :rules="nameRules"
                      :counter="10"
                      label="First name"
                      required
                    ></v-text-field>
                  </v-col>

                  <v-col cols="12" md="6">
                    <v-text-field
                      v-model="formData.lastname"
                      :rules="nameRules"
                      :counter="10"
                      label="Last name"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12" md="6">
                    <v-row>
                      <v-col cols="4" md="3">
                        <!-- <v-combobox
                          v-model="formData.countryCode"
                          :items="['+91', '+222', '+995', '+49', '+233']"
                          variant="outlined"
                        ></v-combobox> -->
                        <vue-country-code @onSelect="onSelect">
                        </vue-country-code>
                      </v-col>

                      <v-col cols="8" md="9">
                        <v-text-field
                          v-model="formData.phoneNumber"
                          :counter="10"
                          label="Phone Number"
                        ></v-text-field> </v-col
                    ></v-row>
                  </v-col>

                  <v-col cols="12" md="6">
                    <v-text-field
                      v-model="formData.email"
                      :rules="emailRules"
                      label="E-mail"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row :class="$vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-2'">
                  <v-col
                    cols="12"
                    :class="{ modalText2: $vuetify.breakpoint.smAndDown }"
                  >
                    <div class="justify-space-between">
                      <div class="text-h5 text--primary my-5">
                        Flight Information
                      </div>
                      <div class="text--primary">
                        Please Provide us flight information
                      </div>
                    </div>
                  </v-col></v-row
                >
                <v-row>
                  <v-col cols="12" md="6">
                    <v-text-field
                      v-model="formData.pnrNum"
                      :rules="nameRules"
                      :counter="10"
                      label="PNR Number"
                      required
                    ></v-text-field>
                  </v-col>

                  <v-col cols="12" md="6">
                    <v-text-field
                      v-model="formData.airlineName"
                      :rules="nameRules"
                      :counter="50"
                      label="Airline name"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12" md="6">
                    <v-menu
                      v-model="menu"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="100%="
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="date"
                          label="Flight Date"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="date"
                        @input="menu = false"
                      ></v-date-picker>
                    </v-menu>
                  </v-col>
                </v-row>

                <v-row :class="$vuetify.breakpoint.smAndDown ? 'mt-0' : 'mt-2'">
                  <v-col
                    cols="12"
                    :class="{ modalText2: $vuetify.breakpoint.smAndDown }"
                  >
                    <div class="justify-space-between">
                      <div class="text-h5 text--primary my-5">
                        Feedback Details
                      </div>
                      <div
                        class="paraText text--primary"
                        :class="{ paraTextSm: $vuetify.breakpoint.smAndDown }"
                      >
                        Thankyou for taking your time to leave us
                        feedback/suggestions pertaining to Chennai Airport.
                        Please Provide us with following details.
                      </div>
                    </div>
                  </v-col></v-row
                >

                <v-row>
                  <v-col cols="12" md="6">
                    <v-select
                      v-model="selectFeedBackType"
                      :items="feedbackTypeData"
                      item-text="value"
                      label="Select"
                      persistent-hint
                      return-object
                      single-line
                      v-on:change="selectFeedback"
                    ></v-select>
                  </v-col>

                  <v-col cols="12" md="6">
                    <v-select
                      v-if="selectFeedBackType.children.length"
                      v-model="selectChildFDBack"
                      :items="selectFeedBackType.children"
                      item-text="value"
                      label="Select"
                      persistent-hint
                      return-object
                      single-line
                      v-on:change="selectChildFeedback"
                    ></v-select>
                  </v-col>

                  <v-col cols="12" md="6">
                    <v-select
                      v-if="selectSubChildFeed.length"
                      v-model="selectSubChildFDBack"
                      :items="selectSubChildFeed"
                      item-text="value"
                      label="Select"
                      persistent-hint
                      return-object
                      single-line
                      v-on:change="selectSubChildFeedback"
                    ></v-select>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12" md="12">
                    <v-textarea
                      v-model="formData.desc"
                      label="Describe Problem in brief..."
                      auto-grow
                      variant="outlined"
                      rows="3"
                      row-height="25"
                    >
                    </v-textarea>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12" md="12">
                    <!-- <v-file-input
                      multiple
                      label="File input"
                      v-model="files1"
                    ></v-file-input> -->
                    <v-file-input
                      show-size
                      label="File input"
                      @change="selectFile"
                    ></v-file-input>
                  </v-col>
                </v-row>

                <!-- <v-row>
                  <v-col cols="12" md="12">
                    <v-file-input
                      label="File input"
                      variant="filled"
                      prepend-icon="mdi-camera"
                    ></v-file-input>
                  </v-col>
                </v-row> -->
                <v-row>
                  <input type="button" value="" />
                </v-row>
                <v-row>
                  <v-card-actions>
                    <v-btn
                      type="submit"
                      block
                      rounded
                      large
                      dark
                      class="gradient-button px-10"
                      >Submit</v-btn
                    >
                  </v-card-actions>
                </v-row>
              </v-container>
            </v-form>
          </div>
        </v-card>
      </div>

      <!-- <div class="" style="height:1500px"></div> -->
    </v-card>
  </div>
</template>
<script>
import axios from "axios";
import rightStickIcon from "../components/rightStickIcon";
export default {
  components: {
    rightStickIcon,
  },

  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      valid: false,
      formData: {
        firstname: "",
        lastname: "",
        email: "",
        pnrNum: "",
        airlineName: "",
        phoneNumber: "",
        countryCode: "",
        desc: "",
        fbtype: null,
      },
      selectFeedBackType: { value: "Select type", children: [] },
      selectChildFDBack: { value: "Select type" },
      selectSubChildFDBack: { value: "Select type" },
      selectSubChildFeed: [],
      files1: [],
      firstname: "",
      lastname: "",
      email: "",
      pnrNum: "",
      airlineName: "",
      fileInfos: [],
      nameRules: [
        (value) => {
          if (value) return true;

          return "Name is requred.";
        },
        (value) => {
          if (value?.length <= 10) return true;

          return "Name must be less than 10 characters.";
        },
      ],

      emailRules: [
        (value) => {
          if (value) return true;

          return "E-mail is requred.";
        },
        (value) => {
          if (/.+@.+\..+/.test(value)) return true;

          return "E-mail must be valid.";
        },
      ],
      feedbackTypeData: [],
      subFeedbackType: [],
      file1: null,
      file2: null,
    };
  },

  computed: {},
  mounted() {
    console.log("Mounted Feedback");
    this.feedbackType();
  },
  methods: {
    handleSubmit(event) {
      event.preventDefault();
      let formData = new FormData();

      // for (let file of this.files1) {
      // formData.append("files", this.file1, this.file1.name);
      // }
      // formData.append("file", this.file1);
      formData.append("images", this.file1);

      formData.append(
        "name",
        this.formData.firstname + " " + this.formData.lastname
      );
      formData.append("mobile[no]", this.formData.phoneNumber);
      formData.append("mobile[code]", this.formData.countryCode);
      formData.append("email", this.formData.email);
      formData.append("pnr", this.formData.pnrNum);
      formData.append("journeyDate", this.date);
      formData.append("airline", this.formData.airlineName);
      formData.append("type", this.selectFeedBackType._id);
      // if (this.selectChildFDBack._id != undefined)
      formData.append("complaint", this.selectChildFDBack._id);
      // if (this.selectSubChildFDBack._id != undefined)
      formData.append("issue", this.selectSubChildFDBack._id);
      // formData.append("nature", this.formData.pnrNum);
      formData.append("description", this.formData.desc);
      formData.append("airport", "MAA");
      formData.append("status", "IN PROGRESS");

      console.log("formData ", formData);
      axios
        .post(
          `${process.env.VUE_APP_AIRLlINE_URL}/user/feedback/update`,
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          console.log("Line 388");
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async feedbackType() {
      let feedbackType = await axios.post(
        `${process.env.VUE_APP_AIRLlINE_URL}/user/feedback-type`,
        {
          page: 1,
          limit: 10,
          filters: {
            main: true,
          },
        }
      );
      console.log("feedbackType", feedbackType.data.data.docs);
      if (feedbackType.data.success) {
        this.feedbackTypeData = feedbackType.data.data.docs;

        // this.fbtype = this.feedbackTypeData[0].value;
      }
      console.log("417", this.feedbackTypeData);
    },
    selectFeedback(item) {
      console.log("selectFeedback", this.selectFeedBackType);
      this.selectSubChildFeed = [];
    },
    selectChildFeedback() {
      console.log("selectChildFeedback", this.selectChildFDBack);
      this.selectSubChildFeed = this.selectChildFDBack.children;
      console.log("this.subFeedbackType", this.selectSubChildFeed);
    },
    selectSubChildFeedback() {
      console.log("selectSubChildFeedback", this.selectSubChildFDBack);
    },
    selectFile(file) {
      // this.progress = 0;
      this.file1 = file;
      console.log("selectFile", file);
    },
    disabledAfterToday(date) {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      return date > today;
    },
    onSelect({ name, iso2, dialCode }) {
      console.log("onSelect");
      console.log(name, iso2, dialCode);
      this.formData.countryCode = dialCode;
    },
  },
};
</script>
<style scoped>
.v-list-item__subtitle {
  margin: 2px 0;
}

.privacyPolicy-list .v-list-item__content p {
  margin-bottom: 0;
}

h1.text--primary.mb-5 {
  line-height: initial;
}

/* .feedbackFormBlock { position: relative; }
.feedbackForm { position: absolute; top:15%; left: 15%; right: 15%;  width:70%; background:#fff; box-shadow: rgba(16, 24, 40, 0.2) 0px 25px 50px -12px;;
border-radius: 24px;max-height: 1500px; border: 1px solid #f1f1f1; margin-bottom: 150px;} */

.feedbackForm {
  width: 70%;
  background: #fff;
  box-shadow: rgba(16, 24, 40, 0.2) 0px 25px 50px -12px;
  border-radius: 24px;
  border: 1px solid #f1f1f1;
  margin: -30% auto 150px;
}

.paraText {
  width: 60%;
}
.paraTextSm {
  width: 100%;
}

.formSection {
  padding: 10px 50px 50px 50px;
}
.formTitle {
  padding: 30px 0;
  background: #e8effe;
  box-shadow: 0px 20px 25px -5px rgba(16, 24, 40, 0.04),
    0px 8px 10px -6px rgba(16, 24, 40, 0.04);
}

.formTitle h1 {
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  letter-spacing: -0.02em;
  color: #111827;
}

/* .feedbackFormSm {  width: 90%; left:5%; right: 5%;max-height: 2300px;} */

.feedbackFormSm {
  width: 90%;
}
.formSectionSm {
  padding: 10px 20px 20px 20px !important;
}
.feedbackFormSm .row + .row {
  margin-top: 0;
}
.v-sheet.v-card {
  box-shadow: none !important;
}

.club-select {
  -webkit-appearance: inherit !important;
  -moz-appearance: none !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.87);
  padding: 10px;
  width: 100%;
  color: rgba(0, 0, 0, 0.87);
  background-image: linear-gradient(45deg, transparent 50%, gray 50%),
    linear-gradient(135deg, gray 50%, transparent 50%),
    linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
}
.club-select:focus {
  background-image: linear-gradient(45deg, green 50%, transparent 50%),
    linear-gradient(135deg, transparent 50%, green 50%),
    linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 15px) 1em, calc(100% - 20px) 1em,
    calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
  border-color: green;
  outline: 0;
}

select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
}

.feedbackFormSm {
  width: 90%;
}
.formSectionSm {
  padding: 10px 20px 20px 20px !important;
}
.feedbackFormSm .row + .row {
  margin-top: 0;
}
.v-sheet.v-card {
  box-shadow: none !important;
}
.gradient-button {
  font-style: normal;
  font-weight: 700;
  font-size: 16px !important;
  line-height: 30px;
  text-align: center;
  color: #ffffff;
  text-transform: capitalize;
}
</style>